@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@400;600;700&display=swap');


h1 {
  color: #fff;
  font-size: 25px !important;

}

h5 {
  color: #fff;
  font-size: 25px !important;
  margin-bottom: 12px;
}

h2 {
  color: #fff;
  font-size: 25px !important;
}


:root {
  --color-main: #f68657;
  --color-text: #383a3f;
  --color-dark: #1f2124;
  --color-gray: #677;
  --color-bg: #f3f6f9;
  --color-light: #acb4bd;
  --color-lighter: #f9f9f9;
  --color-white: #fff;
  --color-border:#e0e3e6;
}

.dark {
  --color-main: #f68657;
  --color-text: #d6d1d1;
  --color-dark: #f5f6f7;
  --color-gray: #999;
  --color-bg: #1f2124;
  --color-lighter: #292a2c;
  --color-white: #2e3235;
  --color-border:#252629;
}

/* BASE STYLES */


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lexend', sans-serif;
  color: inherit;
  font-size: inherit;
  scroll-behavior: smooth;
}


body{
  line-height: 1.8em;
  font-weight: 400;
  font-size: 16px;
}

a {
  text-decoration: none;
}

/* APP STYLES */


.container {
  width: 100%;
  height: 100vh;
  color: var(--color-text);
  background-color: var(--color-bg);
  display: flex;
  align-items: center;
}


.app {
  width: 100%;
  max-width: 1280px;
  height: 88vh;
  margin: 0 auto;
  background-color: var(--color-white);
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.05);
  position: relative;
}


.app-header {
  display: flex;
  align-items: center;
  padding: 16px;
  justify-content: space-between;
  background-color: var(--color-lighter);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.app-header h1 {
  font-size: 30px;
  color: var(--color-dark);
  font-weight: 800;
  text-align: center;
}

.app-header button {
  border: 0;
  background: transparent;
  cursor: pointer;
}

.app-header button > svg {
  fill: var(--color-dark);
  height: 25px;
  width: 25px;
  object-fit: cover;
}

.app-body {
  padding: 16px;
}


/* NOTES STYLES */

.tasks-header {
  background-color: var(--color-white);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 16px;
  padding-bottom: 5px;
}

.tasks-title,
.tasks-count {
  color: var(--color-main);
  font-size: 20px;
  font-weight: 600;
}

.tasks-count {
  font-size: 18px;
  color: var(--color-gray);
}

.task-todo {
  background-color: rgb(221, 105, 125) !important;
  border-radius: 25px;
}

.task-ongoing {
  background-color: rgb(179, 173, 95) !important;
  border-radius: 25px;
}

.task-completed {
  background-color: rgba(35, 81, 207, 0.589) !important;
  border-radius: 25px;
}

.task-reviewed {
  background-color: rgba(5, 170, 13, 0.582) !important;
  border-radius: 25px;
}

.tasks-list-all {
  display: flex;
  justify-content: space-between;
}

.tasks-column {
  padding-left: 10px;
  padding-right: 10px;
  z-index: 900;
  width: 25%;
}

.tasks-list {
  margin: 16px 0;
  height: 75vh;
  overflow-y: auto;
  scrollbar-width: none; /* Firefox */
  
}

.tasks-list::-webkit-scrollbar {
  display: none;
}

.tasks-list-item {
  border: 1px solid transparent;
  margin-bottom: 12px;
  padding: 8px 36px;
  transition: all 0.1s ease-in-out;
}

.tasks-list-item:hover {
  box-shadow: inset 0 0 100px 100px rgba(255, 255, 255, 0.1);
  cursor: pointer;
  border-radius: 25px;
}

.tasks-list-item h3,
.tasks-list-item p span {
  font-weight: 600;
}


.tasks-list-item p span {
  color: var(--color-gray);
  display: inline-block;
  margin-right: 8px;
}

.tasks-list-item p {
  font-size: 14px;
  color: var(--color-light);
}

.floating-button {
  font-size: 48px;
  position: absolute;
  bottom: 24px;
  right: 16px;
  background: var(--color-main);
  border: none;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
}

.tasks-arrows {
  display: flex;
  padding: 0px 15px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  justify-content: space-between;
}


.floating-button > svg {
  fill: var(--color-bg);
}

/*==============================
=>  Task Styles
================================*/

.task-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-main);
  padding: 10px;
}

.task-header h3 {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.task-header h3 svg {
  fill: var(--color-main);
  width: 20px;
  margin-right: 8px;
}

.task-header button {
  border: none;
  outline: none;
  font-weight: 600;
  background-color: transparent;
  font-size: 18px;
  cursor: pointer;
}

.task-detail {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 30px;
  font-size: 24px;
}

.task-droplist {
  font-size: 20px;
  color: black;
}

.task textarea {
  background-color: var(--color-lighter);
  font-size: 20px;
  border-style: inset;
  padding: 16px 12px;
  width: 70%;
  height: 10vh;
  resize: none;
  scrollbar-width: none; /* Firefox */
}

.task-textarea-title {
  height: 50px !important;
  width: 40% !important;
}

.task textarea:active,
.task textarea:focus {
  outline: none;
  border: none;
}

.task textarea::-webkit-scrollbar {
  display: none;
}